"use client"
import Header from '@/components/Header'
import JobCard from '@/components/JobItem/JobCard'
import { useJobsContext } from '@/context/jobContext'
import { Job } from '@/types/types'
import { Bookmark } from 'lucide-react'
import Image from 'next/image'
import { useParams } from 'next/navigation'
import React from 'react'

function page() {
    const {jobs,likeJob}=useJobsContext();
    const params=useParams();
    const {id}=params;
    const [isLiked,setIsLiked]=React.useState(false);
    const job = jobs.find((job: Job) => job._id === id);
    const otherJobs=jobs.filter((job:Job)=>job._id !==id);
    if(!job) return null;
    const {
        title,
        location,
        description,
        salary,
        createdBy,
        applicants,
        jobType,
        createdAt,
        salaryType,
        negotiable,
      } = job;
      const {name,profilePicture}=createdBy;
      const handleLike = (id: string) => {
        setIsLiked((prev) => !prev);
        likeJob(id);
      };
    
  return (
    <div>
      <Header/>
      <div className='p-8 mb-8 mx-auto w-[90%] rounded-md flex gap-8'>
        <div className='w-[26%] flex flex-col gap-8'>
            <JobCard activeJob job={job}/>
            {otherJobs.map((job:Job)=>(
                <JobCard job={job} key={job._id}/>
            ))}
        </div>
        <div className='flex-1 bg-white p-6 rounded-md'>
            <div className='flex justify-between items-center'>
                <div className='flex items-center gap-2'>
                    <Image 
                        src={profilePicture||"/untitled.jpeg"}
                        alt={name}
                        width={50}
                        height={50}
                        className="rounded-md"
                    />
                    <div>
                        <p className='font-bold'>{name}</p>
                        <p className='text-sm'>Recruiter</p>
                    </div>
                </div>
                <button className={`text-2xl ${
                        isLiked?"text-[#7263f3]":"text-gray-400"
                    }`}>
                        {isLiked?<Bookmark size={24}/>:<Bookmark size={24}/>}
                    </button>
            </div>
        </div>
      </div>
    </div>
  )
}

export default page
